  display: flex;
  justify-content: space-between;
  padding-left: 0; }

.social__icon {
  display: block;
  width: 20px;
  height: 20px; }

@media (max-width: 1023px) {
  .social {
    width: 90px; }
  .social__icon {
    width: 15px;
    height: 15px; } }

@media (max-width: 767px) {
  .social {
    margin: 0;
